@media only screen and (min-width: 1024px) {
  section {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 0 2em;
    gap: 0 2em;
    margin-top: 1em;
  }

  .adminnotice {
    padding: 1.5em 6em 0;
  }
}

@media only screen and (min-width: 768px) {
  section .card {
    width: 100%;
  }
}

@media (max-width: 410px) {
  .pinned-button-container .header-button {
    padding: 10px, 24px;
    width: 165px;
    height: 44px;
    font-size: 12px;
  }
}

@media (max-width: 375px) {
  .adminnotice {
    padding: 0 2em 2em;
  }
  .pinned-button-container .pin-text {
    display: flex;
  }
  .pinned-button-container .header-button {
    padding: 10px, 24px;
    width: 145px;
    height: 44px;
    font-size: 12px;
  }
  .pinned-button-container .header-button img {
    margin-left: 4px;
  }
  .pinned-button-container {
    margin-top: 1rem;
  }
}

