* {
  font-family: "Lato";
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-style: normal;
}

:root {
  --button-bg: #00bb7c;
  --button-hover-bg: #01b478;
  --text-color: #333333;
  --button-color: #ffffff;
  --border-color: rgba(0, 0, 0, 0.35);
  --border-hover-color: rgba(0, 0, 0, 0.95);
}

.dashboard-container {
  width: 85%;
  margin: 3em auto;
  color: #333333;
  color: var(--text-color);
  width: 85%;
  margin: 0 auto;
}

.dashboard-container header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.dashboard-container h3.heading {
  font-size: 19px;
  font-weight: 600;
  font-style: normal;
}

body .dashboard-container button {
  width: 175px;
  height: 48px;
  padding: 12px 16px;
  font-size: 16px;
  border: none;
  outline: none;
  background-color: #00bb7c !important;
  background-color: var(--button-bg) !important;
  border-radius: 3px;
  cursor: pointer;
  transition: background-color 0.2s ease;
  text-transform: none;
  font-weight: 600;
  box-shadow: 0px 3px 1px -2px rgb(0 0 0 / 20%),
    0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%);
}

.dashboard-container .active {
  background-color: rgb(235, 235, 235);
}

.dashboard-container button span {
  color: #ffffff;
  color: var(--button-color);
}

.dashboard-container button:hover {
  background-color: #01b478 !important;
  background-color: var(--button-hover-bg) !important;
}

.dashboard-container form > div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.dashboard-container form div .input-group {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: left;
  width: 100%;
}

.dashboard-container form div span:first-of-type {
  margin-right: 20px;
}

.dashboard-container form {
  width: 100%;
  position: relative;
}

.dashboard-container label {
  font-weight: 600;
}

.dashboard-container input:focus {
  border-color: rgba(0, 0, 0, 0.95);
  border-color: var(--border-hover-color);
  outline: none;
}

.dashboard-container form select {
  cursor: pointer;
}

.dashboard-container form .input-group:first-of-type input {
  margin-right: 20px;
}

.toggler {
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.dashboard-container .publish-notice-button {
  display: none;
}

@media screen and (max-width: 768px) {
  .dashboard-container {
    width: 85%;
    margin: 1em auto 2em;
  }

  .dashboard-container header {
    flex-direction: column;
    align-items: flex-start;
  }

  .dashboard-container .heading {
    margin: 15px 0px;
  }

  .dashboard-container header button {
    width: 150px;
    display: none;
  }

  .dashboard-container .publish-notice-button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 12px;
  }
}

@media only screen and (min-width: 1024px) {
  .dashboard-container {
    margin: 3em auto;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --button-color: #ffffff;
  --border-color: rgba(0, 0, 0, 0.1);
  --button-hover-bg: #00bb7c;
  --placeholder-text-color: #333333;
}

label {
  font-weight: 700;
  padding-bottom: 10px;
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.text-editor-wrapper {
  display: block;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-width: 100%; /* set the width of the component */
  height: 50vh; /* set the height of the component */
  overflow-y: hidden;
}

.text-editor {
  display: flex;
  flex-direction: column-reverse;
  height: 90%;
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border: 1px solid var(--border-color);
  box-sizing: border-box !important;
}

.textarea {
  outline: none;
  padding: 15px 24px;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.textarea::-webkit-scrollbar {
  display: none;
}

.toolbarClass {
  display: inline-flex;
  align-items: center;
}

/* style sheet is designed to overwrite the one that comes with the node_module therefore !important is used*/

.rdw-editor-toolbar {
  display: block !important ;
}

.rdw-option-wrapper {
  border: none !important;
  display: inline-flex !important;
}

.rdw-option-wrapper:hover,
.rightIcons:hover {
  background-color: #00bb7c !important;
  background-color: var(--button-hover-bg) !important;
}

.rdw-option-wrapper:active {
  background-color: #00bb7c !important;
  background-color: var(--button-hover-bg) !important;
}
.rdw-option-active {
  background-color: #00bb7c !important;
  background-color: var(--button-hover-bg) !important;
}

.rdw-fontsize-wrapper,
.rdw-emoji-wrapper,
.rdw-inline-wrapper,
.rdw-text-align-wrapper,
.rdw-link-wrapper,
.rdw-list-wrapper,
.rdw-image-wrapper {
  display: inline-block !important;
  border: none !important;
}

.rdw-fontsize-wrapper {
  display: inline-flex !important;
}

.rdw-image-wrapper,
.rdw-emoji-wrapper {
  float: right;
  cursor: pointer;
}

.rdw-emoji-modal,
.rdw-image-modal,
.rdw-link-modal {
  top: -180px !important;
  left: -200px !important;
}

.rdw-dropdown-optionwrapper {
  top: -280px !important;
}

.rdw-image-modal-size-input {
  width: 40% !important;
}

.rdw-image-modal-url-input,
.rdw-link-modal-input {
  width: 100% !important;
}

.rdw-image-modal {
  top: -310px !important;
  left: -200px !important;
}

.rdw-editor-toolbar div {
  display: inline;
  vertical-align: middle;
}

.rdw-dropdown-carettoopen {
  margin: 2px 0px 0px 2px;
}

.rdw-fontsize-wrapper {
  box-shadow: 0px 1px 3px 1px rgb(236, 236, 236);
}

/* To overwrite the default scroll that is added to the textarea*/

.rdw-editor-main {
  overflow: visible !important;
  overflow: initial !important;
}


.rightIcons {
    /* These cover the icons I created */
    border: 1px solid #F1F1F1;
    padding: 5px;
    min-width: 25px;
    height: 20px;
    border-radius: 2px;
    margin: 0 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background: white;
    text-transform: capitalize;
    border: none;
}

.ToggleToolbar-btn{

    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0em;
    color: #E5E5E5;

    /* This style is for the toggletool bar button */
}

.ToggleToolbar-btn-active{
    color: #000;
}


.rightIcons:hover{
    background-color: #00BB7C !important;
}


.rightIcons{
    float: right;
    cursor: pointer;
}



/* makes the tools of the toolbar invisible by default */
.rdw-invisible{
    display: none !important;
} 

/* makes the tools of the toolbar visible */
.rdw-visible{
    display: inline-block !important;
}




